code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.z-10 {
  z-index: 10;
  position: relative;
}
.z-9 {
  position: relative;
  z-index: 9;
}

::selection {
  text-shadow: none;
  background: #d61e3c;
}

body::-webkit-scrollbar {
  width: 10px;
  /* cursor: url('./Components/img/cursor/scrolling.png') 40 10,auto !important; */
}

body::-webkit-scrollbar-track {
  background-color: #e4e4e4;
  border-radius: 100px;
}

body::-webkit-scrollbar-thumb {
  border-radius: 100px;
  border: 2px solid transparent;
  background-clip: content-box;
  background-color: crimson;
}


.App{
  max-width: 100vw;
}

#cropperjs {
  display: block;
  max-width: 100%;
}
 
#preview {
  width: 200px;
  height: 200px;
  margin-bottom: 10px;
  border-radius: 100%;
