            animation-delay: 1.5s;
}
.preloader .FilmToCat img{
    width: 20vw;
}


@-webkit-keyframes FilmToCat {
    0%{
        right: -20vw;
    }
    40%{
        right: 0vw;
    }
    60%{
        right: -20vw;
    }
    100%{
        right: -20vw;
    }
}


@keyframes FilmToCat {
    0%{
        right: -20vw;
    }
    40%{
        right: 0vw;
    }
    60%{
        right: -20vw;
    }
    100%{
        right: -20vw;
    }
}

@media only screen and (max-width: 756px) {
    .preloader .FilmToCat{
        bottom: 8vh;
    }

}
